<template>
	<v-container>
		<v-dialog v-model="dialog" persistent max-width="700px">
			<v-card>
				<v-card-title>
					<span class="headline">{{ formTitle }}</span>
				</v-card-title>
				<v-card-text>
					<v-form ref="form" v-model="valid" lazy-validation>
						<v-container>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="dataPeriodos"
										item-text="periodo"
										item-value="id"
										label="Periodo"
										v-model="editItem.periodo"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
                <v-col cols="12" md="4">
									<v-select
										:items="semanas"
										item-text="text"
										item-value="id"
										label="Semana"
										v-model="editItem.semana"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="paises"
										item-text="text"
										item-value="id"
										label="Pais"
										v-model="editItem.pais"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
							</v-row>
							<v-row>
                <v-col cols="12" md="4">
									<v-autocomplete
										:items="dataPjs"
										item-text="pj_pais"
										item-value="id"
										label="PJ"
										v-model="editItem.pj"
                    @change="select_pj_change"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-autocomplete>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="dataProyectosOcupacion"
										item-text="proyecto"
										item-value="id"
										label="Proyecto"
										v-model="editItem.proyecto"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="username" label="Nombre_Apellido" disabled></v-text-field>
								</v-col>
							</v-row>
							<v-row>
                <v-col cols="12" md="8">
									<v-text-field v-model="editItem.cliente" label="Cliente" disabled></v-text-field>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="editItem.linea_base_horas" type="number" suffix="Hrs" label="Linea Base Horas" @change="change_Lhrs" min="1" :rules="[v => !!v || 'Campo requerido', v => parseFloat(v) > 0 || 'No puede ser menor o igual 0']"
										required></v-text-field>
								</v-col>
							</v-row>
              <v-row>
                <v-col cols="12" md="6">
									<v-text-field v-model="editItem.horas_ejecutadas" type="number" suffix="Hrs" label="Horas Ejecutadas" @change="change_Hrs" :rules="[v => !!v || 'Campo requerido']"
										required></v-text-field>
								</v-col>
								<v-col cols="12" md="6">
									<v-text-field v-model="editItem.dedicacion" type="number" label="Dedicación" prefix="%" disabled></v-text-field>
								</v-col>
							</v-row>			
						</v-container>
					</v-form>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="blue darken-1" text @click="close()">Cancelar</v-btn>
					<v-btn color="blue darken-1" text @click="save()">Guardar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</v-container>
</template>
<script>
  import http from '@/plugins/axios'
  import swal from 'sweetalert'
  import { mapState } from 'vuex'
  export default {
    name: 'EditOcupacion',
    data: () => ({
      valid: true
    }),
    methods: {
      select_pj_change(id){
        let item = this.dataPjs.find(el => el.id === id)
        this.editItem.cliente = item.nombre_cliente
      },
      change_Lhrs(value){
        if (value !== ""){
          this.editItem.dedicacion = parseFloat(this.editItem.horas_ejecutadas / value *100).toFixed(2) 
        }else{
          this.editItem.dedicacion = 0
        }
      },
      change_Hrs(value){
        if (value !== ""){
          this.editItem.dedicacion = parseFloat(value / this.editItem.linea_base_horas *100).toFixed(2) 
        }else{
          this.editItem.dedicacion = 0
        }
      },      
      close() {
        this.$store.commit('updateShowEdit')
				this.$store.commit('updateEditItem', {})
				this.$store.commit('setFormTitle', '')
				this.$refs.form.resetValidation()
      },
      put(url, params){
        http.put(url, params)
        .then((response) => {
          let data = response.data
          this.$store.commit("updateDataOcupacion", data)
          swal("Actualizado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
					swal("Error Interno", "Favor contactar con el administrador", "error");
        });
			},
			post(url, params){
				http.post(url, params)
					.then((response) => {
						let data = response.data
						this.$store.commit("newOcupacion", data)
						swal("Creado Correctamente!", {
							icon: "success",
						});
					})
					.catch(() => {
						swal("Error Interno", "Favor contactar con el administrador", "error");
					});
      },  
      save(){
				if (this.$refs.form.validate()){
					this.editItem.pm = this.username
					let proyecto = this.editItem.proyecto
					let periodo = this.editItem.periodo
					let pj = this.editItem.pj
					if (typeof(proyecto) !== "object"){
						this.editItem.proyecto = {"id": proyecto}
					}else{
						this.editItem.proyecto = {"id": this.editItem.proyecto.id}
					}
					if (typeof(periodo) !== "object"){
						this.editItem.periodo = {"id": periodo}
					}else{
						this.editItem.periodo = {"id": this.editItem.periodo.id}
					}
					if (typeof(pj) !== "object"){
						this.editItem.pj = {"id": pj}
					}else{
						this.editItem.pj = {"id": this.editItem.pj.id}
					}
					if (this.formTitle === 'Editar'){
						this.put(`api/ocupacion/${this.editItem.id}`, this.editItem)
					}else{
						this.post('api/ocupacion', this.editItem)
					}
					this.close()
				}
      },
    },
    computed: {
      ...mapState(['dialog', 'dataProyectosOcupacion', 'editItem', 'dataPeriodos', 'dataPjs', 'editItemIndex', 'paises', 'semanas', 'formTitle', 'username']),
			paises(){
				let paises = this.$store.getters.getPaises;
				let paises_pm = this.$store.getters.getPaisPM;
				let paisesSelect = paises.filter(
					function(e){
						let index = paises_pm.indexOf(e.text)
						return index !== -1
					})
				return paisesSelect
			}
		},
  };
</script>