<template>
	<v-container>
		<v-dialog v-model="dialog" persistent max-width="700px">
			<v-card>
				<v-card-title>
					<span class="headline">{{ formTitle }}</span>
				</v-card-title>
				<v-card-text>
					<v-form ref="form" v-model="valid" lazy-validation>
						<v-container>
							<v-row>
								<v-col cols="12" md="4">
									<v-select
										:items="dataPeriodos"
										item-text="periodo"
										item-value="id"
										label="Periodo"
										v-model="editItem.periodo"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="paises"
										item-text="text"
										item-value="id"
										label="Pais"
										v-model="editItem.pais"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="dataProyectos"
										item-text="proyecto"
										item-value="id"
										label="Proyecto"
										v-model="editItem.proyecto"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
							</v-row>
							<v-row>
                <v-col cols="12" md="8">
									<v-autocomplete
										v-model="editItem.servicio"
										:items="itemsServicios"
										item-text="servicio"
										label="Servicio"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-autocomplete>
								</v-col>
								<v-col cols="12" md="4">
									<v-select
										:items="tipos"
										label="Tipo"
										v-model="editItem.tipo"
										:rules="[v => !!v || 'Campo requerido']"
										required
									></v-select>
								</v-col>
							</v-row>
							<v-row>
                <v-col cols="12" md="4">
									<v-text-field v-model="editItem.registrados" label="Registrados" type="number" :rules="[v => !!v || 'Campo requerido']"></v-text-field>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="editItem.cerrados" type="number" label="Cerrados" :rules="[v => !!v || 'Campo requerido']"
										required></v-text-field>
								</v-col>
								<v-col cols="12" md="4">
									<v-text-field v-model="editItem.backlog" type="number" label="Backlog" :rules="[v => !!v || 'Campo requerido']"
										required></v-text-field>
								</v-col>
							</v-row>
              <v-row>
								<v-alert dense type="info" outlined>
									<strong color="sucess">Backlog y Registrados</strong> y la 	<strong>Eficiencia</strong> son calculados automaticamente.
								</v-alert>
							</v-row>
						</v-container>
					</v-form>
				</v-card-text>
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn color="blue darken-1" text @click="close()">Cancelar</v-btn>
					<v-btn color="blue darken-1" text @click="save()">Guardar</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
		<v-snackbar
      v-model="snackbar"
      :timeout="timeout"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="red"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
	</v-container>
</template>
<script>
  import http from '@/plugins/axios'
  import swal from 'sweetalert'
  import { mapState } from 'vuex'
  export default {
    name: 'EditVolumetria',
    data: () => ({
			valid: true,
			loading: false,
			loader: null,
			snackbar: false,
			text: '',
			timeout: 3000,
			itemsServicios: [],
    }),
    methods: {     
      close() {
        this.$store.commit('updateShowEdit')
				this.$store.commit('updateEditItem', {})
				this.$store.commit('setFormTitle', '')
				this.$refs.form.resetValidation()
				this.loading = false
      },
      put(url, params){
        http.put(url, params)
        .then((response) => {
          let data = response.data
          this.$store.commit("updateDataVolumetrias", data)
          swal("Actualizado Correctamente!", {
            icon: "success",
          });
        })
        .catch(() => {
					swal("Error Interno", "Favor contactar con el administrador", "error");
        });
			},
			post(url, params){
				http.post(url, params)
					.then((response) => {
						let data = response.data
						this.$store.commit("newVolumetria", data)
						swal("Creado Correctamente!", {
							icon: "success",
						});
					})
					.catch(() => {
						swal("Error Interno", "Favor contactar con el administrador", "error");
					});
      },  
      save(){
				if (this.$refs.form.validate()){
					let proyecto = this.editItem.proyecto
					let periodo = this.editItem.periodo
					if (typeof(proyecto) !== "object"){
						this.editItem.proyecto = {"id": proyecto}
					}else{
						this.editItem.proyecto = {"id": this.editItem.proyecto.id}
					}
					if (typeof(periodo) !== "object"){
						this.editItem.periodo = {"id": periodo}
					}else{
						this.editItem.periodo = {"id": this.editItem.periodo.id}
					}
					if (this.formTitle === 'Editar'){
						this.put(`api/volumetrias/${this.editItem.id}`, this.editItem)
					}else{
						this.post('api/volumetrias', this.editItem)
					}
					this.close()
				}
			},
    },
    computed: {
			...mapState(['dialog', 'dataProyectos', 'editItem', 'dataPeriodos', 'dataPjs', 'editItemIndex', 'paises', 'semanas', 'formTitle', 'tipos']),
			paises(){
				let paises = this.$store.getters.getPaises;
				let paises_pm = this.$store.getters.getPaisPM;
				let paisesSelect = paises.filter(
					function(e){
							let index = paises_pm.indexOf(e.text)
							return index !== -1
					})
				return paisesSelect
			}
		},
		watch: {
			"editItem.proyecto": function(value){
				if (value !== '' && value !== undefined){
					let proyecto;
					if(typeof(value) === 'object'){
						proyecto = value.id
					}else{
						proyecto = value
					}
					http.get('api/servicios/'+proyecto)
					.then(r => r.data)
					.then(response => {
						this.itemsServicios = response;
					})
					.catch(e => {
						console.log(e)
					})
				}
			}
		},
  };
</script>